#NotFound {
  min-height: calc(100vh - 208px);
}
#NotFound > h1 {
  color: var(--secondary);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: clamp(2rem, 5vw, 5rem);
  text-align: center;
}

